@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:600,700);
html {
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif; }

*,
*:before,
*:after {
  box-sizing: inherit; }

.app {
  height: 95vh;
  position: relative;
  max-width: 700px;
  margin: 10px auto;
  box-shadow: 10px 10px 40px 23px #e0e0e0;
  border-radius: 12px; }
  .app .header {
    max-height: 12vh;
    display: flex;
    align-items: center;
    padding: 10px 25px;
    color: #e8ecfd;
    background-image: -webkit-gradient(linear, left top, right top, from(#5b7cf9), color-stop(#5274f4), color-stop(#4a6bef), color-stop(#4163e9), to(#375be4));
    background-image: -webkit-linear-gradient(left, #5b7cf9, #5274f4, #4a6bef, #4163e9, #375be4);
    background-image: linear-gradient(to right, #5b7cf9, #5274f4, #4a6bef, #4163e9, #375be4);
    border-top-left-radius: 12px;
    border-top-right-radius: 12px; }
    .app .header__icon {
      max-width: 50px;
      height: auto;
      opacity: 0.7; }
    .app .header__title {
      margin-left: auto;
      font-size: 25px;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 4.5px; }
      .app .header__title span {
        display: block;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 1.5px; }
  .app .list {
    display: flex;
    flex-direction: column;
    max-height: 83vh;
    padding: 20px 0 40px;
    overflow-y: scroll; }
    .app .list__person {
      display: flex;
      min-height: 80px;
      align-items: center;
      padding: 10px 15px;
      margin: 10px 15px;
      border-radius: 4px;
      box-shadow: 0 2px 18px 7px rgba(189, 189, 189, 0.21);
      -webkit-transition: all 0.25s linear;
      transition: all 0.25s linear;
      -webkit-animation: fadeIn 0.5s 0.1s both;
              animation: fadeIn 0.5s 0.1s both; }
      .app .list__person .person__image {
        position: relative;
        max-width: 77px;
        height: auto;
        margin-right: 22px;
        border: 10px solid #f5f6fa;
        border-radius: 50%; }
      .app .list__person .person__row {
        display: flex;
        flex-direction: column; }
        .app .list__person .person__row__name {
          font-size: 23px;
          color: #979cb0; }
        .app .list__person .person__row__sets {
          font-size: 12px; }
      .app .list__person .person__rating {
        margin-left: auto;
        display: flex;
        flex-direction: column; }
      .app .list__person .person__rating-value {
        font-size: 26px;
        font-weight: 700;
        color: #4cd5af; }
      .app .list__person .person__rating-delta {
        display: flex;
        margin-left: auto;
        font-size: 12px; }
        .app .list__person .person__rating-delta .delta-value__up {
          color: #45d984; }
        .app .list__person .person__rating-delta .delta-value__down {
          color: #ff6b6d; }
      .app .list__person:hover {
        -webkit-transform: scale(1.02);
                transform: scale(1.02); }
    .app .list::-webkit-scrollbar {
      width: 12px;
      cursor: pointer; }
    .app .list::-webkit-scrollbar-track {
      background-color: #fefefe;
      border-radius: 12px; }
    .app .list::-webkit-scrollbar-thumb {
      height: 68px;
      border-radius: 14px;
      background-color: #f1f2f6;
      cursor: pointer; }

@media screen and (max-width: 430px) {
  .app {
    max-width: 320px; }
    .app .list__person {
      padding: 4px 8px; }
      .app .list__person .person__image {
        max-width: 56px;
        border-width: 4px; }
      .app .list__person .person__name {
        font-size: 16px; }
      .app .list__person .person__rating-value {
        font-size: 20px; }
      .app .list__person .person__rating-stats {
        font-size: 9px; } }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; } }

@keyframes fadeIn {
  from {
    opacity: 0; } }

